.container
{
  max-width:1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px,1fr));
  grid-gap: 20px;
  box-sizing: border-box;
  padding:20px 0 50px;
}

.container .box
{
  position: relative;
  padding:140px 40px 40px;
  transition:0.5s;
  text-align: center;
  background:#fff;
  border-radius:10px;
}


.container .box p
{
  margin:0;
  padding:0;
  transition:0.5s;

}

.container .box h4
{
  margin:20px 0 0;
  padding:0;
  transition:0.5s;
  font-size:18px;
  color:#0099ff;
  font-weight:700;
  line-height: 20px;
  text-transform: uppercase;
}

.container .box h4 span
{
  font-weight: 600;
  font-size: 14px;
  color: #c5c5c5;
  transition: 0.5s;
}
section.banner-whatsapp {
    padding: 0;
}

.container .box .imgBox
{
  position:absolute;
  top: 0px;
  left: 50%;
  transform: translate(-50%);
  width: 120px;
  height: 120px;
  border-radius:50%;
  overflow: hidden;

}

.slick-prev:before, .slick-next:before {
    color: #555;
}

.slick-slider div { transition: none; }

